import React from 'react';
import NarrowFrame from '../NarrowFrame';
import Description from '../Description';

import './style.scss';
import Phone from '../Phone';

interface WorkProps {
  img?: string;
  title: string;
  tags: string[];
  phone?: boolean;
}

const Work:React.FC<WorkProps> = ({ img, title, tags, phone = false, children }) => {
  return (
    <div className={`Work ${phone ? 'Work--centered' : ''}`}>
      {
        !phone && (
          <NarrowFrame>
            <img src={img} alt={title} />
          </NarrowFrame>
        )
      }
      {
        phone && (
          <Phone videoSrc={img!} />
        )
      }
      <Description
        title={title}
        tags={tags}
        children={children} />
    </div>
  );
};

export default Work;